.App{
  min-height: 100vh;
  background: linear-gradient(270deg, #ed00df, #6904a9, #2313d6, #e5078a, #f80c19, #def604);
  background-size: 1200% 1200%;  
	-webkit-animation: Gradient 80s ease infinite;
	-moz-animation: Gradient 80s ease infinite;
	animation: Gradient 80s ease infinite;
}

@-webkit-keyframes Gradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@-moz-keyframes Gradient {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@keyframes Gradient { 
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}


  .heading{
    background-color: rgb(7, 151, 55);
    font-style: normal;
    font-size: medium;
    max-width: fit-content;
    padding: 10px;
    border-radius: 5vh;
    box-shadow: slategray;
    color: white;
     
}

.loading{
  background-color: rgb(7, 151, 55);
  font: message-box;
  font-size: large;
  max-width: fit-content;
  border-radius: 3vh;
  padding: 10px;
  box-shadow: black;
  color: aliceblue;
  flex: content;
  justify-self: center;
  
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background-color: rgb(7, 151, 55);
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
